.pagination{
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 40px 0 70px;
	ul{
		display: flex;
		justify-content: center;
		align-items: center;
		margin: 0 20px;
		a{
			padding: 13px 11px;
			font-weight: 600;
			font-size: 18px;
			line-height: 18px;
			color: #747676;
			border-bottom: 2px solid transparent;
			transition: .3s;
			&:hover{
				color: #1E1F1F;
			}
		}
		.active{
			border-color: #F74B4B;
			color: #1E1F1F;
		}
	}
	&__prev,
	&__next{
		width: 55px;
		height: 30px;
		display: flex;
		position: relative;
		align-items: center;
		img{
			margin-left: 5px;
		}
		//.img{
		//	width: 40px;
		//	height: 10px;
		//	position: absolute;
		//	top: calc(50% - 5px);
		//	left: 0;
		//	z-index: 50;
		//	transition: .3s;
		//	transition-delay: .4s;
		//
		//}
		//&:hover{
		//	img{
		//		z-index: 1;
		//
		//	}
		//	.img{
		//		left: 30px;
		//		width: 21px;
		//		fill: #fff;
		//	}
		//	.action-button__circle--base{
		//		transform: rotate(480deg);
		//		fill:  #373737;
		//		circle{
		//			fill: #373737;
		//			transition: .3s;
		//			transition-delay: 0.5s;
		//		}
		//	}
		//}

		&:hover{
			.action-button__circle--base{
				transform: scale(0.2) rotate(480deg);
				fill:  #373737;
				circle{
					fill: #373737;
					transition: .3s;
					transition-delay: 0.3s;
				}
			}
		}

	}
	.action-button__circle{
		position: absolute;
		top: 0;
		right: 0;
		circle{
			stroke-width: 1;
		}
	}
	.action-button__circle--base{
		transform: rotate(210deg);
		transition: .5s;
		circle{
			stroke-width: 1;
			stroke: #373737;
			fill: transparent;
			stroke-dasharray: 210;
			stroke-dashoffset: 140;

		}
	}
	.action-button__circle--fill{
		display: none;
	}
	&__next{
		justify-content: flex-end;
		.action-button__circle{
			left: 0;
		}
		.action-button__circle--base{
			transform: rotate(30deg);
		}
		img{
			transform: rotate(180deg);
			margin-left: 0;
			margin-right: 5px;
		}
	}
}
